@import url('http://getbootstrap.com/dist/css/bootstrap.css');

.navbarcontainer {
    width: 100%;
    height: 50px;
    background-color: #262262;
    display: flex;
    color: #fff;
    justify-content: space-between;
}

.headersection {
    height: 100%;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    gap: 1rem;
    margin-left: 20px;
    align-content: center;
    cursor: pointer;
}

.header {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    align-content: center;
}

.header img {
    width: 2.2rem;
    object-fit: contain;
}

.header h3 {
    font-weight: 600;
}

.header p {
    margin-left: 20px;
    margin-top: 5px;
    text-decoration: none;
    font-size: 13px;
    color: #fff;
}

.rightheadersection {
    display: flex;
    align-items: center;
    gap: 1rem;
}

.rightheadersection .createdeal {
    cursor: pointer;
    border: 1px solid #3c3c3c;
    background-color: #262262;
    padding: 6px 12px;
    border-radius: 5px;
    font-size: 0.8rem;
    /* margin-right: 10px; */
    align-items: center;
}

.rightheadersection .createdeal:hover {
    background-color: #221a79;
}

.userdropdown {
    position: relative;
    display: inline-flex;
    gap: 1rem;
    height: 100%;
    align-items: center;
    margin-right: 25px;
    cursor: pointer;
}

.userdropdown p {
    text-transform: capitalize;
}

.userdropdown .circle {
    width: 25px;
    height: 25px;
    border-radius: 2rem;
    border: 1px none #000000;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #fff;
    color: #000;
}

.dropdownMenu {
    display: block;
    position: absolute;
    top: 60px;
    right: 0;
    width: 170px;
    background-color: #fff;
    color: #262262;
    border: 1px solid #ccc;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
    border-radius: 4px;
    z-index: 1000;
    padding: 6px 0;
}

.dropdownMenu::before {
    content: "";
    position: absolute;
    top: -10px;
    right: 20px;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    border-bottom: 10px solid #fff;
    z-index: 1001;
}

.dropdownMenu::after {
    content: "";
    position: absolute;
    top: -11px;
    right: 20px;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    border-bottom: 10px solid #ccc;
    z-index: 1000;
}

.dropdownMenu button {
    display: block;
    width: 100%;
    background: none;
    border: none;
    padding: 5px 20px;
    text-align: left;
    color: black;
    font-size: inherit;
    cursor: pointer;
}

.dropdownMenu button:hover {
    background-color: #312b69;
    color: #fff;
    border-radius: 5px;
    padding: 5px 20px;
    margin: 0 3px;
    width: 96%;
}